.add-campaign-form{
  width: 500px;
  height: 368px;
  margin: 20px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  position: relative;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
  text-align: center;

  .campaign-sides-container{
    display:flex;
    .left-side,
    .middle-side,
    .right-side{
      width: 250px;
      height: calc(100% - 50px);
      margin-top: 20px;
      margin-bottom: 20px;
      .input-box{
        display: flex;
        flex-direction: column;
        padding: 10px;
        width: 90%;
        .label{
          text-align: left;
          font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
          font-size: 12px;
          color: #333333;
        }
        .input-container{
          height: 30px;
          .rw-widget{
            font-size: 12px;
          }
          input{
            width: 95%;
            height: 24px;
          }
          .rw-select-list-label{
            text-align: left;
            input{
              width: auto;
            }
          }
          .rw-popup-container{
            text-align: left;
          }
          textarea{
            width: 95%;
            height: 48x;
          }
        }
      }
    }
    .left-side,
    .middle-side{
      margin-right: 20px;
      border-right: 1px solid darkgray;
    }
  }
  .add-button{
    position: absolute;
    bottom: 10px;
    right: 10px;
    height: 20px;
    padding: 10px;
    font-size: 14px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    border-radius: 4px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border: 1px solid gray;
    color: lightgray;
  &.enabled{
      cursor: pointer;
      border: 1px solid green;
      color: darkgreen;
    }
    &:hover{
      border: none;
      background-color: green;
      color: white;
    }
  }
}